import React, { FC, CSSProperties, useRef } from "react";
import clsx from "clsx";

import { ReactComponent as ArrowDown } from "assets/icons/expand-more.svg";

import styles from "./style.module.scss";
import { Content } from "./types";

export interface SummaryClasses {
  summary: string;
  details: string;
  root: string;
}

export interface SummaryProps extends Content {
  expandAction: (detailsHeight: number) => void;
  isOpen?: boolean;
  offset?: number;
  isApplyOffset?: boolean;
  classes?: Partial<SummaryClasses>;
}

const Summary: FC<SummaryProps> = ({
  summary,
  details,
  isOpen,
  expandAction,
  offset = 0,
  isApplyOffset,
  classes,
}) => {
  const detailsRef = useRef<HTMLDivElement>(null);

  let offsetStyle: CSSProperties | undefined;
  if (isApplyOffset) {
    offsetStyle = {
      transform: `translateY(${offset}px)`,
    };
  }

  return (
    <div className={clsx(styles.root, classes?.root)} style={offsetStyle}>
      <div
        onClick={() => {
          if (detailsRef.current) {
            expandAction(detailsRef.current.clientHeight);
          }
        }}
        className={clsx(styles.summary, classes?.summary, {
          [styles.summaryOpen]: isOpen,
        })}>
        <span className={styles.summaryText}>{summary}</span>
        <ArrowDown
          className={clsx(styles.expandIcon, {
            [styles.expandIconOpen]: isOpen,
          })}
        />
      </div>
      <div
        ref={detailsRef}
        className={clsx(styles.details, classes?.details, {
          [styles.detailsOpen]: isOpen,
        })}>
        <span>{details}</span>
      </div>
    </div>
  );
};

export default Summary;
