import React, { FC, CSSProperties, useState, useRef } from "react";
import clsx from "clsx";

import Summary from "./Summary";
import { AccordionProps } from "./types";
import styles from "./style.module.scss";

const Accordion: FC<AccordionProps> = ({
  contents,
  summaryClasses,
  className,
}) => {
  const [expandedSummary, setExpandedSummary] = useState({
    offset: 0,
    indexNumber: 0,
  });
  const accordionRef = useRef<HTMLDivElement>(null);
  const previousOffset = useRef(0);

  let containerStyle: CSSProperties | undefined;
  if (accordionRef.current) {
    const containerHeight =
      accordionRef.current.clientHeight -
      previousOffset.current +
      expandedSummary.offset;
    containerStyle = {
      height: containerHeight,
      maxHeight: containerHeight,
    };
    previousOffset.current = expandedSummary.offset;
  }

  const expandHandler = (detailsHeight: number, indexNumber: number) => {
    const isClose = expandedSummary.indexNumber === indexNumber;
    setExpandedSummary({
      offset: isClose ? 0 : detailsHeight,
      indexNumber: isClose ? 0 : indexNumber,
    });
  };

  return (
    <div
      ref={accordionRef}
      className={clsx(styles.root, className)}
      style={containerStyle}>
      {contents.map(({ summary, details }, index) => {
        const indexNumber = index + 1;
        return (
          <Summary
            key={`${summary}_${index}`}
            summary={summary}
            details={details}
            expandAction={(detailsHeight) =>
              expandHandler(detailsHeight, indexNumber)
            }
            isOpen={expandedSummary.indexNumber === indexNumber}
            isApplyOffset={indexNumber > expandedSummary.indexNumber}
            offset={expandedSummary.offset}
            classes={summaryClasses}
          />
        );
      })}
    </div>
  );
};

export default Accordion;
